import { MAX_GUESSES } from "./Game";

export const colors = [
  "#ffffff",
  "#c42348",
  "#33ca7f",
  "#ea7317",
  "#3da5d9",
  "#fec601"
];

export function measure(percent: number) {
  const ROW_HEIGHT = 100 / MAX_GUESSES;
  return `${ROW_HEIGHT * (percent / 100)}vh`;
}
