import React from "react";
import { seqArray } from "./arrayUtils";
import { colors, measure } from "./Visual";
import { Feedback } from "./Game";

interface IProps {
  feedback: Feedback;
}

export default function FeedbackView(props: IProps) {
  return (
    <div className="feedback-view" style={{ padding: `${measure(5)} 0` }}>
      <FeedbackViewRow count={props.feedback.red} color={colors[1]} />
      <FeedbackViewRow count={props.feedback.white} color={colors[0]} />
    </div>
  );
}

function FeedbackViewRow(props: { count: number; color: string }) {
  return (
    <div className="feedback-row" style={{ height: measure(35) }}>
      {seqArray(0, props.count).map(() => (
        <span
          className="feedback-peg"
          style={{
            backgroundColor: props.color,
            height: measure(25),
            width: measure(25),
            marginRight: measure(10),
            marginTop: measure(5),
            marginBottom: measure(5)
          }}
        ></span>
      ))}
    </div>
  );
}
