import React from "react";
import { colors, measure } from "./Visual";

interface IProps {
  onClick: () => void;
  disabled: boolean;
  warning?: boolean;
}

export default function CheckButton(props: IProps) {
  const color = props.disabled ? "#bbb" : props.warning ? colors[5] : colors[2];
  return (
    <button
      className="check-button"
      {...props}
      style={{
        color,
        borderColor: color,
        height: measure(80),
        width: measure(80),
        fontSize: measure(45),
        marginLeft: measure(10)
      }}
    >
      âœ”
    </button>
  );
}
