import React, { useEffect, useState } from "react";
import { Combo, Game } from "./Game";
import getInfo, { GameInfo } from "./GameInfo";

interface IProps {
  gameInfo: GameInfo;
}

export default function InfoView(props: IProps) {
  const info = props.gameInfo;

  return (
    <div className="info">
      {info.headline && <h1>{info.headline}</h1>}
      <p>{info.text}</p>
      {info.warning && <p className="warning">{info.warning}</p>}
    </div>
  );
}
