import React from "react";
import { seqArray } from "./arrayUtils";
import CheckButton from "./CheckButton";
import { colors, measure } from "./Visual";
import FeedbackView from "./FeedbackView";
import { Combo, COMBO_SIZE, Feedback, Guess, UNSELECTED } from "./Game";

interface IProps {
  combo: Combo | null;
  feedback: Feedback | null;
  onClick: (index: number) => void;
  active?: boolean;
  warning?: boolean;
  submit: () => void;
  index: number;
}

export default function Row(props: IProps) {
  let rowClass = "row";
  if (props.active) rowClass += " active";

  return (
    <div
      className={rowClass}
      style={{ height: measure(100), padding: measure(10) }}
    >
      <span
        className="row-label"
        style={{ width: measure(60), marginTop: measure(20) }}
      >
        {props.active ? ">>" : `${props.index + 1}]`}
      </span>
      {seqArray(0, COMBO_SIZE).map(index => (
        <span
          className="dot"
          style={{
            height: measure(80),
            width: measure(80),
            margin: `0 ${measure(10)}`,
            backgroundColor: props.combo
              ? colors[props.combo[index]]
              : undefined
          }}
          onClick={() => props.onClick(index)}
        ></span>
      ))}
      <div
        className="feedback-area"
        style={{ width: measure(35 * COMBO_SIZE + 15) }}
      >
        {props.active && (
          <CheckButton
            onClick={() => props.submit()}
            disabled={!!props.combo?.some(val => val == UNSELECTED)}
            warning={props.warning}
          />
        )}
        {props.feedback && <FeedbackView feedback={props.feedback} />}
      </div>
    </div>
  );
}
