import React, { useEffect, useState } from "react";
import { seqArray } from "./arrayUtils";
import {
  COMBO_SIZE,
  Game,
  gameLost,
  gameWon,
  MAX_GUESSES,
  newGame,
  submitGuess,
  UNSELECTED
} from "./Game";
import Row from "./Row";
import produce from "immer";
import Palette from "./Palette";
import InfoView from "./InfoView";
import { measure } from "./Visual";
import getInfo, { GameInfo } from "./GameInfo";

interface IProps {}

export default function Board(props: IProps) {
  const [game, setGame] = useState(newGame());
  const [activeCombo, setActiveCombo] = useState(newBlankCombo());
  const [activeValue, setActiveValue] = useState(1);

  const [gameInfo, setGameInfo] = useState<GameInfo>({ text: "" });
  useEffect(() => {
    setGameInfo(getInfo(game, activeCombo));
  }, [game, activeCombo]);

  const gameOver = gameWon(game) || gameLost(game);

  const reset = () => {
    setGame(newGame());
    setActiveCombo(newBlankCombo());
  };

  const handleGuess = () => {
    setGame(submitGuess(game, activeCombo));
    setActiveCombo(newBlankCombo());
  };

  return (
    <div className="board" style={{ fontSize: measure(40) }}>
      <div className="rows">
        {game.guesses.map((guess, index) => (
          <Row
            key={index}
            {...guess}
            index={index}
            onClick={() => {}}
            submit={() => {}}
          />
        ))}
        {!gameOver && (
          <Row
            index={game.guesses.length}
            combo={activeCombo}
            feedback={null}
            active
            warning={!!gameInfo.warning}
            onClick={index =>
              setActiveCombo(
                produce(activeCombo, ac => {
                  ac[index] = activeValue;
                })
              )
            }
            submit={() => handleGuess()}
          />
        )}
      </div>
      <div className="sidebar" style={{ width: measure(400) }}>
        <Palette
          onClick={index => setActiveValue(index)}
          activeValue={activeValue}
        />
        <InfoView gameInfo={gameInfo} />
        {gameOver && (
          <button
            className="replay"
            style={{ height: measure(80) }}
            onClick={() => reset()}
          >
            Play again
          </button>
        )}
      </div>
    </div>
  );
}

function newBlankCombo() {
  return new Array(COMBO_SIZE).fill(UNSELECTED);
}
