import produce from "immer";
import {
  Combo,
  COMBO_SIZE,
  feedbackCompare,
  Game,
  gameLost,
  gameWon,
  generateFeedback,
  Guess,
  MAX_GUESSES,
  NUM_COLORS,
  UNSELECTED
} from "./Game";

export interface GameInfo {
  headline?: string;
  text: string;
  warning?: string;
}

export default function getInfo(game: Game, activeCombo: Combo): GameInfo {
  if (gameWon(game)) return winInfo(game);

  if (gameLost(game)) return lossInfo(game);

  return midGameInfo(game, activeCombo);
}

function winInfo(game: Game): GameInfo {
  return {
    headline: "Win!",
    text: `${game.guesses.length} guesses.`
  };
}

function lossInfo(game: Game): GameInfo {
  return {
    headline: "Loss :(",
    text: `${possibleCombosRemaining(game).length} possible combos remain.`
  };
}

function midGameInfo(game: Game, activeCombo: Combo): GameInfo {
  const warnRow = warningRow(activeCombo, game.guesses);
  return {
    text: `${possibleCombosRemaining(game).length} possible combos remain.`,
    warning:
      warnRow == null
        ? undefined
        : `Current guess cannot be the solution. See Row ${warnRow + 1}.`
  };
}

function warningRow(activeCombo: Combo, guesses: Guess[]): number | null {
  if (activeCombo.some(val => val == UNSELECTED)) return null;

  for (let i = 0; i < guesses.length; ++i) {
    const guess = guesses[i];
    if (
      feedbackCompare(
        generateFeedback(activeCombo, guess.combo),
        guess.feedback
      ) !== 0
    )
      return i;
  }
  return null;
}

export function possibleCombosRemaining(game: Game): Combo[] {
  const combos = allPossibleCombos();
  const possCombos = combos.filter(possCombo =>
    game.guesses.every(
      guess =>
        feedbackCompare(
          generateFeedback(possCombo, guess.combo),
          guess.feedback
        ) == 0
    )
  );
  return possCombos;
}

export function allPossibleCombos(): Combo[] {
  const combos: Combo[] = [];
  let combo: Combo = new Array(COMBO_SIZE).fill(0);
  combos.push(combo);
  const MAX_VAL = NUM_COLORS - 1;
  while (combo.some(val => val < MAX_VAL)) {
    let i = 0;
    while (true) {
      if (combo[i] == MAX_VAL) {
        combo = produce(combo, combo => {
          combo[i] = 0;
        });
        ++i;
      } else {
        combo = produce(combo, combo => {
          combo[i] += 1;
        });
        break;
      }
    }
    combos.push(combo);
  }
  return combos;
}
