export const COMBO_SIZE = 4;
export const MAX_GUESSES = 10;
export const NUM_COLORS = 6;
export const UNSELECTED = -1;

export type Combo = number[];

export interface Feedback {
  red: number;
  white: number;
}

export type Guess = {
  combo: Combo;
  feedback: Feedback;
};

export interface Game {
  solution: Combo;
  guesses: Guess[];
}

export function newGame(): Game {
  const solution: Combo = [];
  for (let i = 0; i < COMBO_SIZE; ++i) {
    solution.push(Math.floor(Math.random() * NUM_COLORS));
  }
  return {
    solution,
    guesses: []
  };
}

export function submitGuess(game: Game, combo: Combo) {
  const guess = { combo, feedback: generateFeedback(combo, game.solution) };
  return { ...game, guesses: [...game.guesses, guess] };
}

export function generateFeedback(a: Combo, b: Combo): Feedback {
  const red = a.reduce(
    (sum, _, index) => (a[index] == b[index] ? sum + 1 : sum),
    0
  );
  const total = overlap(valueCountize(a), valueCountize(b));
  return {
    red,
    white: total - red
  };
}

function valueCountize(nums: number[]): number[] {
  return nums.reduce((valueCounts: number[], value) => {
    valueCounts[value] = (valueCounts[value] || 0) + 1;
    return valueCounts;
  }, []);
}

function overlap(a: number[], b: number[]): number {
  return a.reduce(
    (count: number, _, index) => count + (Math.min(a[index], b[index]) || 0),
    0
  );
}

export function feedbackCompare(a: Feedback, b: Feedback) {
  return 10 * (a.red - b.red) + (a.white - b.white);
}

export function gameWon(game: Game): boolean {
  return (
    game.guesses.length > 0 &&
    game.guesses[game.guesses.length - 1].feedback.red == COMBO_SIZE
  );
}

export function gameLost(game: Game): boolean {
  return game.guesses.length == MAX_GUESSES && !gameWon(game);
}
