import React from "react";
import { seqArray } from "./arrayUtils";
import { colors, measure } from "./Visual";
import { NUM_COLORS } from "./Game";

interface IProps {
  onClick: (index: number) => void;
  activeValue: number;
}

export default function Palette(props: IProps) {
  return (
    <div className="palette">
      {seqArray(0, NUM_COLORS).map(index => (
        <span
          className="palette-item"
          style={{
            backgroundColor: colors[index],
            height: measure(80),
            width: props.activeValue == index ? measure(90) : measure(80)
          }}
          onClick={() => props.onClick(index)}
        ></span>
      ))}
    </div>
  );
}
